.landingpage-parent {
  position: relative;
  width: 100%;
  height: 100%;
}
@keyframes scale-down-center {
0% {
  -webkit-transform: scale(1);
  transform: scale(1);
}
to {
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
}
}
.background {
position: absolute;
height: 100%;
width: 100%;
top: 0px;
right: 0px;
bottom: 0px;
left: 0px;
background-color: var(--color-darkslategray-100);
overflow: hidden;
}
.gradientbg-icon {
position: absolute;
height: 780px;
width: 100%;
top: 50px;
right: 0px;
bottom: 0px;
left: 0px;
overflow: hidden;
max-height: 100%;
object-fit: cover;
}
.logocontent{
position: absolute;
top: 50px;
bottom: 0px;
left: 0px;
right: 0px;
width: 100%;
height: 100%;
}
.setlogo-icon {
position: absolute;
top: 0px;
left: calc(50% - 556.6px);
width: calc(1336px/1.2);
height: calc(888px/1.2);
object-fit: cover;
}
.blurbg {
position: absolute;
top: 50px;
left: 0px;
background-color: var(--color-gainsboro);
backdrop-filter: blur(27px);
width: 1454px;
height: 951px;
display: none;
}
.desktop2 {
position: absolute;
align-items: center;
width: 100%;
height: 780px;
overflow: hidden;
z-index: 3;
}
.about {
position: relative;
}
.about-frame {
z-index: 3;
position: absolute;
top: 143px;
left: 71px;
width: 222px;
display: flex;
flex-direction: row;
align-items: center;
justify-content: center;
padding: var(--padding-21xl);
box-sizing: border-box;
font-size: var(--font-size-41xl);
font-family: var(--font-kaushan-script);
}
.aboutcontent {
position: relative;
display: flex;
align-items: center;
justify-content: center;
width: 764px;
flex-shrink: 0.1;
z-index: 3;
}
.aboutcontent-wrapper {
position: absolute;
width: calc(100% - 676px);
top: 310px;
right: 605px;
left: 71px;
display: flex;
flex-direction: row;
align-items: center;
justify-content: flex-start;
padding: var(--padding-21xl) 0px;
box-sizing: border-box;
font-size: var(--font-size-5xl);
font-family: var(--font-caladea);
}
.aboutwhole {
position: absolute;
width: 100%;
top: 780px;
right: 0px;
left: 0px;
background-color: var(--color-black);
height: 780px;
overflow: hidden;
text-align: center;
font-size: var(--font-size-5xl);
color: var(--color-white);
font-family: var(--font-caladea);
}
.particlescontainer{
position: absolute;
width: 100%;
top: 780px;
right: 0px;
left: 0px;
background-color: var(--color-black);
height: 780px;
overflow: hidden;
z-index: 2;
}
.glassmorph{
  z-index: 3;
}
a.social{
  position: relative;
  padding: 14px;
  color: white;
  cursor: pointer;
  svg{
    opacity: 0.6;
    &:hover{
      filter:opacity(0.95) drop-shadow(1px 2px 2px var(--glow-color));
      opacity: 1;
    }
  }
}
